    font: 500 14px/18px "Roboto", sans-serif;
    color: #323642;
    letter-spacing: 1px;
    text-transform: uppercase;
    position: relative;
  }
  .header-content .menu-list > li > a:before {
    content: "";
    display: block;
    width: calc(100% - 10px);
    height: 3px;
    background-color: #4CB649;
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    opacity: 0;
    transition: 0.4s;
  }
  .header-content .menu-list > li:hover > a:before {
    opacity: 1;
  }
  .header-content .menu-list > li.parent {
    position: relative;
  }
  .header-content .menu-list > li.parent > a {
    padding-right: 21px;
  }
  .header-content .menu-list > li.parent > a:after {
    content: "\e905";
    font-family: icomoon;
    display: block;
    font-size: 23px;
    color: #323642;
    position: absolute;
    top: -2px;
    right: -6px;
  }
  .header-content .menu-list > li.parent:hover > .menu-second {
    opacity: 1;
    pointer-events: auto;
  }
  .header-content .menu-list > li.active > a:before {
    opacity: 1;
