.toggle-hamburger {
  display: block;
  background-color: unset;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 24px;
  height: 16px;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border: none;
  border-radius: unset;
  cursor: pointer;
}
.toggle-hamburger:focus {
  outline: none;
}
.toggle-hamburger span {
  display: block;
  position: absolute;
  transform: translate(0, -50%);
  top: 50%;
  width: 100%;
  height: 2px;
  background: #22252E;
  border-radius: 3px;
}
.toggle-hamburger span:before, .toggle-hamburger span:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #22252E;
  display: block;
  position: absolute;
  left: 0;
  border-radius: 3px;
}
.toggle-hamburger span:before {
  top: -7px;
}
.toggle-hamburger span:after {
  bottom: -7px;
}
.toggle-hamburger.toggle-hamburger__animx span {
  transition: background 0s 0.4s;
}
.toggle-hamburger.toggle-hamburger__animx span:before, .toggle-hamburger.toggle-hamburger__animx span:after {
  transition-duration: 0.4s, 0.4s;
  transition-delay: 0.4s, 0s;
}
.toggle-hamburger.toggle-hamburger__animx span:before {
  transition-property: top, transform;
}
.toggle-hamburger.toggle-hamburger__animx span:after {
  transition-property: bottom, transform;
}
.toggle-hamburger.toggle-hamburger__animx.active span {
  background: none;
}
.toggle-hamburger.toggle-hamburger__animx.active span:before, .toggle-hamburger.toggle-hamburger__animx.active span:after {
  transition-delay: 0s, 0.4s;
}
.toggle-hamburger.toggle-hamburger__animx.active span:before {
  top: 0;
  transform: rotate(45deg);
}
.toggle-hamburger.toggle-hamburger__animx.active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}

.toggle-hamburger.toggle-hamburger__animx.active span:before, .toggle-hamburger.toggle-hamburger__animx.active span:after {
  transition-delay: 0s, 0.4s;
}
.toggle-hamburger.toggle-hamburger__animx.active span:before {
  top: 0;
  transform: rotate(45deg);
}
.toggle-hamburger.toggle-hamburger__animx.active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}

